<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/standard" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">操作标准</span>
        </el-menu-item>
        <el-menu-item index="/recordTask"   >
          <i class="el-icon-caret-right"></i>
          <span slot="title">任务记录</span>
        </el-menu-item>
        <el-menu-item index="/maintain" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">维护记录</span>
        </el-menu-item>

        <el-menu-item index="/problem" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">问题反馈</span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 120px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div style="padding: 20px 0 0 20px;width: 100%">
      <div style="padding-bottom: 20px">
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
        <el-button type="info" plain @click="exportData">批量导出</el-button>
      </div>
      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" ></el-table-column>
        <el-table-column label="序号" prop="id" align="center"></el-table-column>
        <el-table-column label="设备名称" prop="equipmentName" align="center"></el-table-column>
        <el-table-column label="任务包名称"  prop="packageName" align="center" ></el-table-column>
<!--        <el-table-column label="任务编号"  width="170" prop="taskNum" align="center"></el-table-column>-->
        <el-table-column label="任务类型"  prop="taskType" align="center"></el-table-column>
        <el-table-column label="领取人员"  prop="receivePeople" align="center"></el-table-column>
        <el-table-column label="开始时间"  prop="startTime" align="center"></el-table-column>
        <el-table-column label="完成时间"  prop="time" align="center"></el-table-column>
        <el-table-column label="问题反馈" prop="feedbackDescription" align="center">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="redirectTocheckRecordInfo(scope.row)">查看</el-button>
            <!--          <el-button size="mini" type="danger" plain @click="viewCheckRecord(scope.row.packageId)">问题详情</el-button>-->
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="新增点检记录" :visible.sync="fromVisible" width="30%">
        <el-form :model="form" label-width="100px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="设备类型" prop="type"><el-input v-model="form.type" placeholder="设备类型"></el-input></el-form-item>
<!--          <el-form-item label="任务包编号" prop="packageId"><el-input v-model="form.packageId" placeholder="任务包编号"></el-input></el-form-item>-->
            <el-form-item label="任务包名称" prop="packageName"><el-input v-model="form.packageName" placeholder="任务包名称"></el-input></el-form-item>
            <el-form-item label="领取人员" prop="receivePeople"><el-input v-model="form.receivePeople" placeholder="领取人员"></el-input></el-form-item>
            <el-form-item  label="任务类型" prop="taskType"><el-input  v-model="form.taskType" placeholder="任务类型"></el-input></el-form-item>

        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CheckRecord",
  data() {
    return {
      searchText: '',
      tableData:[],
      value1: '',
      packageId:'',
      packageIds:'',
      packageName:'',
      type:'',
      time:'',
      equipmentNum:'',
      equipmentName:'',
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      total: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        packageNum: [
          { required: true, message: '请输入任务包编号', trigger: 'blur' },
        ]
      },
      ids: []

    }
  },
  created() {
    this.load(this.$route.query.taskId)
    // let equipmentNum = this.$route.query.equipment_num;
    // this.equipmentNum=this.$route.query.equipment_num;
    // console.log("equipmentNum is"+this.equipmentNum);
    // if (equipmentNum) {
    //   // 如果存在 equipment_num 参数，则根据该参数进行查询
    //   this.loadByEquipmentNum(equipmentNum);
    // } else {
    //   // 如果没有 equipment_num 参数，则显示全部数据
    //   this.load();
    // }

  },
  methods: {
    loadByEquipmentNum(equipmentNum) {
      // 发起根据 equipment_num 查询数据的请求
      this.$request.get('/checkRecord/selectByEquipmentNum', {
        params: {
          equipmentNum: this.equipmentNum,
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },




    redirectTocheckRecordInfo(row) {
      // 使用 $router.push() 方法将参数作为查询参数传递给目标页面
      this.$router.push({ path: '/checkRecordInfo', query: { id: row.id, packageId: row.packageId,packageName:row.packageName,taskNum:row.taskNum}});
    },
    async loadByPackageIds(packageIds) {
      try {
        let allRecords = [];
        // 使用 Promise.all 同时发送多个请求以提高效率
        const promises = packageIds.map(packageId => {
          return this.$request.get('/checkRecord/selectByPackageIds', {
            params: {
              packageId: packageId,
              pageNum: this.pageNum,
              pageSize: this.pageSize
            }
          });
        });
        // 等待所有请求完成
        const responses = await Promise.all(promises);
        // 将所有记录合并到一个数组中
        responses.forEach(response => {
          allRecords = allRecords.concat(response.data.records);
        });
        this.tableData = allRecords;
        this.total = allRecords.length;
      } catch (error) {
        console.error(error);
      }
    }
    ,

    exportData(){   //批量导出数据
      if (!this.ids.length){  //没有选择行的时候，全部导出，或者根据我的搜索条件导出
        window.open(this.$baseUrl + '/checkRecord/export?token=' + this.user.token + "&packageNum=" + this.packageNum)
      } else {   //[1,2,3] => '1,2,3'
        let idStr = this.ids.join(',')
        window.open(this.$baseUrl + '/checkRecord/export?token=' + this.user.token + '&ids=' + idStr)

      }

    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/checkRecord/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/checkRecord/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form = {}  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/checkRecord/update': '/checkRecord/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(taskId) {  // 分页查询
      this.$request.get('/checkRecord/get', {
        params: {
          taskId: taskId
        }
      }).then(res => {
        this.tableData = res.data
        console.log(this.tableData)

        return this.$request.get('/deviceList/getName', {
          params: {
            equipmentNum: this.tableData.equipmentNum

          }
        })
      }).then(res => {
        this.equipmentName = res.data
        console.log(this.equipmentName)
      }).catch(error => {
        console.error(error)
      })
    },

    load1(pageNum) {// 分页查询
      if (pageNum)
        this.pageNum = pageNum
      this.$request.get('/checkRecord/selectByEquipmentNum', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          equipmentNum: this.equipmentNum,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    async handleCurrentChange(pageNum) {
      if (this.equipmentNum ===undefined) {
        this.load(pageNum)
      } else {
        console.log(" ppp equipmentNum is"  + this.equipmentNum);
this.load1(pageNum)
      }
    },

  }
}
</script>

<style scoped>

</style>